<template>
  <div class="botupchat">
    <div class="botupchat-comment-section-chat">
      <div v-if="conversations.to" class="botupchat-comment-section-replay" :style="UIPrefs.comment_reply_section.css">
        <div class="botupchat-bg-reply" :style="UIPrefs.chat_bg_reply.css">
          <p v-html="conversations.to" class="margin-zero" :style="UIPrefs.reply_para.css"></p>
        </div>
        <p class="botupchat-small-2" :style="UIPrefs.small_reply.css">{{ conversations.time }}</p>
      </div>
      <div v-if="conversations.from" class="botupchat-message-box">
        <div class="d-flex align-items-center">
          <div>
            <b-img :src="UIPrefs.chat_header.attr.gravatar" class="rounded-circle mr-3" width="30"></b-img>
          </div>
          <div class="botupchat-bg-chat" :style="UIPrefs.chat_bg_color.css">
            <p :style="UIPrefs.marginzero.css">{{ conversations.from }}</p>
          </div>
        </div>
        <p class="botupchat-small-2" :style="UIPrefs.small_two.css">{{ conversations.time }}</p>
      </div>
      <div v-if="conversations.image" class="botupchat-message-box">
        <div>
          <img :src="conversations.image" class="rounded-circle" />
        </div>
        <p class="botupchat-small-2" :style="UIPrefs.small_two.css">{{ conversations.time }}</p>
      </div>
      <div v-if="conversations.video" class="botupchat-message-box">
        <div :style="UIPrefs.mcq_video.css">
          <b-link :href="conversations.video" target="_blank" :style="UIPrefs.mcq_video_link.css">
            {{ conversations.video }}
          </b-link>
        </div>
        <p class="botupchat-small-2" :style="UIPrefs.small_two.css">{{ conversations.time }}</p>
      </div>
      <div v-if="conversations.redirect" class="botupchat-message-box">
        <div class="botupchat-bg-chat" :style="UIPrefs.chat_bg_color.css">
          <b-link :href="conversations.redirect" target="_blank" class="small">
            {{ conversations.redirect }}
          </b-link>
        </div>
        <p class="botupchat-small-2" :style="UIPrefs.small_two.css">{{ conversations.time }}</p>
      </div>
      <div v-if="conversations.cases" class="botupchat-comment-section-chat">
        <div class="botupchat-mcq" :style="UIPrefs.chat_mcq.css">
          <h6 class="margin-zero" :style="UIPrefs.chat_mcq_info.css">{{ conversations.information }}</h6>
          <img v-if="conversations.imageMCQ" :src="conversations.imageMCQ" :style="UIPrefs.chat_mcq_img.css" />
          <div v-for="(value, index) in conversations.cases" :key="index">
            <b-button pill class="botupchat-btn-light" :style="UIPrefs.button_bg.css" :ref="`MCQ-${Object.values(value)[0]}-${index}`" v-on:click="emit_MCQ_visitor_response(value, index)"> {{ Object.keys(value)[0] }} </b-button>
          </div>
        </div>
        <p class="botupchat-small-2" :style="UIPrefs.small_reply.css">{{ conversations.time }}</p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: ["conversations"],
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
      };
    },
    mounted() {},
    methods: {
      /**
       * Trigger when user select MCQ questions
       * @param {Object} value  Get respective obj value
       * @param {Number} id  Get index
       */
      emit_MCQ_visitor_response(value, id) {
        // Remove element based on id
        this.conversations.cases.filter((item, index) => {
          if (index !== id) this.$refs[`MCQ-${Object.values(item)[0]}-${index}`][0].remove();
        });

        // Set the count based on id which is selected by visitor
        var MCQ_count = id == 0 ? this.conversations.cases.length - 1 : this.conversations.cases;

        // Remaining cases should be deleted
        this.conversations.cases.splice(
          this.conversations.cases.findIndex((element, index) => index !== id),
          MCQ_count,
        );

        // Save the active message in state
        window._Botup.Bot.saveInState("setActiveMessage", value);
      },
    },
  };
</script>



