<template>
  <body>
    <div id="botupchat" class="body-scroll" :style="UIPrefs.bodyscroll.css">
      <div class="botupchat-chat-section botupchat-bg-img" :style="UIPrefs.chat_section_padding.css">
        <div class="botupchat-comment-section-chat">
          <div class="botupchat-message-box">
            <div class="botupchat-bg-chat" style="background: #ededed;" :style="UIPrefs.chat_bg_color.css">
              <p :style="UIPrefs.marginzero.css">
                {{ UIPrefs.chat_header.attr.bot_welcome_msg }}
                <b-img :src="UIPrefs.chat_header.attr.welcome_img" width="20" height="20"></b-img>
              </p>
            </div>
            <p class="botupchat-small-2" :style="UIPrefs.small_two.css"></p>
          </div>
        </div>
        <Message v-for="(list, index) in conversations" :key="index" :conversations="list"></Message>
      </div>
    </div>
  </body>
</template>
<script>
  import Message from "./Message.vue";
  export default {
    components: {
      Message,
    },
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
      };
    },
    mounted() {
      // Automatically scroll down in onload
      this.scrollToEnd();
    },
    computed: {
      conversations() {
        // Automatically scroll down in onload
        this.scrollToEnd();
        // Get data from mutation state through veux
        return window._Botup.State.getters.getCurrentMessages;
      },
    },
    methods: {
      // Automatically scroll down while updating conversation
      scrollToEnd() {
        setTimeout(() => {
          const container = document.getElementById("botupchat");
          if (container) container.scrollTop = container.scrollHeight;
        }, 0);
      },
    },
  };
</script>



