<template>
  <div id="app">
    <div class="botupchat" :style="UIPrefs.chat.css">
      <Header> </Header>
      <div :style="UIPrefs.min_height.css">
        <Messages></Messages>
      </div>
      <div style="positin:fixed;">
        <InputEditor></InputEditor>
        <Footer></Footer>
      </div>
    </div>
  </div>
</template>
<script>
  import Header from "./Window/Header";
  import Footer from "./Window/Footer";
  import Messages from "./Window/Messages.vue";
  import InputEditor from "./Window/InputEditor.vue";

  export default {
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
        receiverMessage: {},
      };
    },
    components: {
      Header,
      Footer,
      Messages,
      InputEditor,
    },

    mounted() {},
    created() {},
    methods: {},
  };
</script>



